"use server"

import { type FormState } from "@/validations/auth";

// use server is not display in the console because actions are run on the server
// actions are run on the server side
export async function registerUserAction(prevState: FormState , formData: FormData) {
    const fields = {
        username: formData.get("username") as string,
        email: formData.get("email") as string,
        password: formData.get("password") as string,
    }

    console.log(fields)

    return {
        fields,
    }
}